import { useState } from "react"
import { useEffect } from "react";
import { useContext } from "react"
import { AppContext } from "../App"

export function Cell(props) {
  const { state, dispatch } = useContext(AppContext)
  const d = props.data ? props.data.options.disabled : false
  const [value, setValue] = useState("")
  const [selected, setSelected] = useState(false)
  const [spellcheckerError, spellcheckerErrorArray] = useState([])
  const [contextMenu, setContextMenuStatus] = useState(false)

  const change = (e) => {
    const val = e.target.value;
    setValue(val)
    dispatch({
      type: "setCell",
      payload: {
        position: props.position,
        value: val
      }
    })
  }

  const selecting = (e, val) => {
    dispatch({
      type: "selectingCell",
      payload: {
        node: e.target.closest('.cell-wrapper'),
        position: props.position,
        value: val
      }
    })
  }

  const copy = () => {
    const SC = state.company[props.position.p].table.selectedCell
    if (SC) {
      const SCvalue = state.company[props.position.p].table.getCell(SC.x, SC.y)
      if (SC.x === props.position.x) { // только в своей колонке
        dispatch({
          type: "setCell",
          payload: {
            position: props.position,
            value: SCvalue.data
          }
        })
      }
    }
  }

  const spellcheck = (e) => {
    if (props.spellcheck) {
      const word = e.target.value
      fetch('https://speller.yandex.net/services/spellservice.json/checkText?text=' + word)
        .then(res => res.json())
        .then(res => {
          spellcheckerErrorArray(res)
        })
    }
  }

  const getFrequency = (n) => {
    const MPval = state.method.split(" ")[1]
    const MP = MPval !== undefined ? MPval : 8

    if (n >= 5000) return "СВЧ"
    else if (n >= 1000) return "ВЧ"
    else if (n >= 100) return "СЧ"
    else if (n >= MP) return "НЧ"
    else return "МП"
  }

  const rightClick = (e) => {
    e.preventDefault()
    setContextMenuStatus(true)
  }

  const onFocus = () => {
    dispatch({
      type: "setCellOnFocus",
      payload: props.position
    })
  }

  useEffect(() => {
    // Если изменяюетя YW пересчитываем частотность
    if (+props.position.p === 0 && +props.position.x === 3) {
      const v = value === "" ? props.data.data : value
      dispatch({
        type: "updateFrequency",
        payload: {
          part_ind: props.position.p,
          row_ind: props.position.y,
          value: getFrequency(+v)
        }
      })
    }
  }, [value])

  return (
    <CellOptions {...props} d={d}>
      <label
        onContextMenu={rightClick}
        onMouseDown={(e) => selecting(e, true)}
        onMouseUp={(e) => selecting(e, false)}
        onMouseMove={copy}>

        <SpellcheckerErrorIndicator d={spellcheckerError} />
        {contextMenu && <ContextMenu {...props} close={() => setContextMenuStatus(false)} />}

        <input
          type={props.type}
          onFocus={onFocus}
          onBlur={spellcheck}
          className="cell-input"
          value={props.data !== undefined ? props.data.data : ""}
          onChange={change}
          disabled={d} />
        {selected && <div className="selected-marker" onClick={(e) => null}></div>}
      </label>
    </CellOptions>
  )
}

function SpellcheckerErrorIndicator(props) {
  if (props.d.length === 0) return null;
  else {
    return (
      <div className="spellchecker-error-indicator">
        <i class="bi bi-question-octagon"></i>
      </div>
    )
  }
}

function ContextMenu(props) {
  const { state, dispatch } = useContext(AppContext)
  const value = props.data !== undefined ? props.data.data : ""

  const click = () => {
    if (value.length > 0) {
      dispatch({
        type: "addItemToBBufer",
        payload: value
      })
      props.close()
    }
  }

  return (
    <div className="card context-menu">
      <div className="card-body">
        <div onClick={click} className="context-menu__item">Добавить в B-буфер</div>
      </div>
      <div className="card-footer">
        <div onClick={props.close} className="context-menu__item">Закрыть</div>
      </div>
    </div>
  )
}

function CellOptions(props) {

  const beforeOptions = () => {
    if (props.part === "0" && props.col === "0") {
      return <ActionOption key="00" {...props} icon="bi bi-eye" />
    }
    if (props.part === 1 && props.col === 3) {
      return <ActionOption key="13" {...props} icon="bi bi-eye" />
    }
  }

  const afterOptions = () => {
    const options = [];

    if (props.title === "Заголовки") {
      options.push(
        <ColorTitleCounter {...props} />
      )
    }

    return options;
  }

  function ColorTitleCounter(props) {
    const l = props.data !== undefined ? props.data.data.length : 0
    let color = ""

    if (l <= 50 && l > 0) color = "#0dcd0d" // green
    else if (l > 50 && l < 56) color = "orange"
    else if (l >= 56) color = "red"
    else if (l < 1) color = "#cccccc"

    return (
      <div key="color" className="cell-option color-title-counter">
        <span style={{ color: color }}>{l}</span>
      </div>
    )
  }

  return (
    <div className="cell-wrapper" disabled={props.d}>
      <div className="before">{beforeOptions()}</div>
      {props.children}
      <div className="after">{afterOptions()}</div>
    </div>
  )
}

function ActionOption(props) {

  const { state, dispatch } = useContext(AppContext);

  const action = () => {
    dispatch({
      type: 'toggleRowDisable',
      payload: { position: props.position, value: props.d }
    })
  }

  return (
    <>
      <span onClick={action} className={`row-number btn${props.d ? " disabled" : ""}`}>{props.index + 1}</span>
    </>
  )
}