import React, { useContext, useState } from "react";
import { useEffect } from "react";
import { AppContext } from "../App";
import { Link } from "react-router-dom";

export function ControlBar_1(props) {
  return (
    <div className="sidebar">
      <Btn />
      <div className="delimeter"></div>
      {/* <Btn1 /> */}
      <Btn2 />
      <CutSquareBrackets {...props} />
      <BtnReplace {...props} />
      <Btn3 />
      <div className="delimeter"></div>
      <BtnClearAll {...props} />
    </div>
  )
}

function Btn(props) {
  const { state, dispatch } = useContext(AppContext);

  const click = () => {
    dispatch({
      type: "createPart2Data"
    })
  }

  return (
    <div className="widget">
      <Link onClick={click} to="/part_2" className="btn">На второй шаг</Link>
    </div>
  )
}

function BtnClearAll(props) {
  const { state, dispatch } = useContext(AppContext);

  const clearAll = () => {
    dispatch({
      type: "clearAll",
      payload: props.part
    })
  }

  return (
    <div className="widget">
      <div onClick={clearAll} className="btn">Очистить всё</div>
    </div>
  )
}

function CutSquareBrackets(props) {
  const { state, dispatch } = useContext(AppContext);

  return (
    <div className="widget">
      <div onClick={() => {
        dispatch({
          type: "cutSquareBrackets",
          payload: props.part
        })
      }} className="btn">[*]</div>
    </div>
  )
}

function BtnReplace(props) {
  const { state, dispatch } = useContext(AppContext);
  const [open, toggleOpen] = useState(false)
  const [from, setFrom] = useState('')
  const [to, setTo] = useState('')

  const changeFrom = (e) => {
    setFrom(e.target.value)
  }

  const changeTo = (e) => {
    setTo(e.target.value)
  }

  const replace = () => {
    dispatch({
      type: 'replace',
      payload: {
        part_index: props.part,
        from,
        to
      }
    })
    const cells = document.querySelectorAll('.cell-input')
    cells.forEach(c => {
      c.classList.remove('find-word')
    })
  }

  const changeColBarItem = (colIndex) => {
    dispatch({
      type: "changeColFromReplaceBar",
      payload: {
        part_index: props.part,
        col_index: colIndex,
        value: state.company[props.part].table.colIndexesToReplace[colIndex]
      }
    })
  }

  const colChangerBar = () => {
    let bar = [];
    for (let i = 0; i < state.company[props.part].table.colsCount; i++) {
      bar.push(
        <div
          key={i}
          onClick={() => changeColBarItem(i)}
          className={`col-icon ${state.company[props.part].table.colIndexesToReplace[i] ? 'changed' : ''}`}>
        </div >
      )
    }
    return (
      <div className="col-changer-bar">
        {bar}
      </div>
    )
  }

  useEffect(() => {
    if (from !== "") {
      const cells = document.querySelectorAll('.cell-input')
      cells.forEach(c => {
        if (c.value.indexOf(from) >= 0) {
          c.classList.add('find-word')
        }
        else {
          c.classList.remove('find-word')
        }
      })
    }
  }, [from])

  return (
    <div className="widget">
      <div onClick={() => toggleOpen(!open)} className="btn">{'ABC > XYZ'}</div>
      {open &&
        <div className="replace-form">
          <div className="d-flex">
            <label>
              <input placeholder="найти" onChange={changeFrom} type="text" value={from} />
            </label>
            <label>
              <input placeholder="и заменить" onChange={changeTo} type="text" value={to} />
            </label>
            <div onClick={replace} className="btn mr5">Заменить</div>
            <div onClick={() => toggleOpen(false)} className="btn">
              <i className="bi bi-x-lg"></i>
            </div>
          </div>
          {colChangerBar()}
        </div>
      }
    </div>
  )
}

function Btn1(props) {
  const { state, dispatch } = useContext(AppContext);
  const [menu, openMenu] = useState(false);

  const toggleMenu = () => {
    openMenu(!menu);
  }

  const change = (n) => {
    dispatch({
      type: 'updateOption',
      payload: {
        key: 'walletCount',
        value: state.walletCount + n
      }
    })
  }

  return (
    <div className="widget">
      <div onClick={toggleMenu} className="btn has-submenu">
        {!menu ? 'Кол-во объявлений' : 'Закрыть'}
        {!menu && <span className="badge">{state.walletCount}</span>}</div>
      {menu &&
        <div className="sidebar-submenu">
          <div onClick={() => change(1)} className="btn icon"><i className="bi bi-plus"></i></div>
          <span className="wallet-count" style={{ fontSize: '1.5rem', margin: '5px', display: 'block' }}>{state.walletCount}</span>
          <div onClick={() => change(-1)} className="btn icon"><i className="bi bi-dash"></i></div>
        </div>
      }
    </div>
  )
}

function Btn2(props) {
  const { state, dispatch } = useContext(AppContext);
  const [menu, openMenu] = useState(false);
  const [submenu, openSubmenu] = useState(false);

  const toggleMenu = () => {
    if (menu) {
      openSubmenu(false)
    }
    openMenu(!menu);
  }

  const change = (n) => {
    dispatch({
      type: 'updateOption',
      payload: {
        key: 'method',
        value: n
      }
    })
    toggleMenu();
  }

  return (
    <div className="widget">
      <div onClick={toggleMenu} className="btn has-submenu">
        {!menu ? state.method : 'Закрыть'}
        {false && <span className="badge">{state.method}</span>}</div>
      {menu &&
        <div className="sidebar-submenu">
          <div
            onClick={() => openSubmenu('SKAG')}
            style={{ fontSize: '1rem', marginBottom: '10px' }}
            className={`btn ${submenu === 'SKAG' && 'changed'}`}>SKAG</div>
          {submenu === 'SKAG' &&
            <div className="sidebar-submenu">
              <div onClick={() => change('SKAG 10')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">10</div>
              <div onClick={() => change('SKAG 15')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">15</div>
              <div onClick={() => change('SKAG 20')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">20</div>
            </div>
          }
          <div
            onClick={() => change('STAG')}
            style={{ fontSize: '1rem', marginBottom: '10px' }}
            className={`btn ${submenu === 'STAG' && 'changed'}`}>STAG</div>
        </div>
      }
    </div>
  )
}

function Btn3(props) {
  const { state, dispatch } = useContext(AppContext);
  const [menu, openMenu] = useState(false);
  const [submenu, openSubmenu] = useState(false);

  const toggleMenu = () => {
    if (menu) {
      openSubmenu(false)
    }
    openMenu(!menu);
  }

  const change = (n) => {
    dispatch({
      type: 'updateOption',
      payload: {
        key: 'quotable',
        value: n
      }
    })
    toggleMenu();
  }

  const style = { fontSize: '1rem', marginBottom: '10px', width: '100%' }

  return (
    <div className="widget">
      <div onClick={toggleMenu} className="btn has-submenu">
        {!menu ? state.quotable : 'Закрыть'}
      </div>
      {menu &&
        <div className="sidebar-submenu">
          <div onClick={() => change('СВЧ')} style={style} className="btn">СВЧ</div>
          <div onClick={() => change('СВЧ/ВЧ')} style={style} className="btn">СВЧ/ВЧ</div>
          <div onClick={() => change('Нет')} style={style} className="btn">Не ставить кавычки</div>
        </div>
      }
    </div>
  )
}