

export function FAQ() {
  return (
    <ul className="faq part">
      <li>Для <b>распространения значения</b> ячейки в столбце, выделите нужную ячейку и, удерживая ЛКМ, двигайтесь вверх или вниз;</li>
      <li>Кнопка "Очистить всё" полностью <b>очищает</b> текущую таблицу;</li>
      <li>Чтобы <b>отключить</b> строку, нажмите на её номер;</li>
      <li><b>ПКМ</b> на ячейке открывает контекстное меню;</li>
      <li><b>B-буфер</b> предназначен для хранения и вставки нескольких значений ячеек;</li>
      <li>Чтобы <b>воспользоваться значением из B-буфера</b> нажмите на ячейку, в которую хотите вставить значение и нажмите на выбранное значение в буфере;</li>
    </ul>
  )
}