export function reducer(state, action) {
  const [type, pay] = [action.type, action.payload]
  const pos = pay ? pay.position : null
  const table = pos ? state.company[pos.p].table : null
  pos &&
    console.log(pos);

  switch (type) {
    case 'copyFromClipboard':
      const {
        part, col
      } = {
        ...action.payload
      };
      let data = action.payload.data.split('\r\n');
      for (let i = 0; i < data.length; i++) {
        state.company[part].table.setCol(col, data)
        if (part == 0 && col == 1 && state.company[part].table.getCell(+col + 1, i).data === "") { // Если название компании в строке пустое, копируем из групп
          state.company[part].table.setCell(+col + 1, i, data[i])
        }
      }
      break

    case 'toggleRowDisable':
      const {
        p, x, y
      } = {
        ...action.payload.position
      }
      if (state.company[p] !== undefined) {
        state.company[p].table.toggleRowDisable(y, action.payload.value);
      }
      break

    case 'updateOption':
      window.o[pay.key] = pay.value
      state[pay.key] = pay.value

      if (pay.key === "quotable") {
        pay.value === "Нет" ? state.company[0].table.unquotable() : state.company[0].table.quotable(pay.value)
      }

      break

    case "selectingCell":
      state.company[pay.position.p].table.setSelectedCell(pay.value ? pay.position : null)
      break

    case "copyModOff":
      state.company.forEach((t) => {
        t.table.setSelectedCell(null)
      })
      break

    case "setCell":
      const pos = pay.position
      state.company[pos.p].table.setCell(pos.x, pos.y, pay.value)
      break

    case "clearCol":
      state.company[pay.part].table.clearCol(pay.col)
      break

    case "clearAll":
      state.company[pay].table.clearAll()
      break

    case "sortColumn":
      state.company[pay.part_index].table.sortColumn(pay.col_index, pay.sort_title)
      break

    case "cutSquareBrackets":
      state.company[pay].table.cut_square_brackets()
      break

    case "replace":
      state.company[pay.part_index].table.replace(pay.from, pay.to)
      break

    case "changeColFromReplaceBar":
      state.company[pay.part_index].table.changeColFromReplaceBar(pay.col_index, pay.value)
      break

    case "updateFrequency":
      state.company[pay.part_ind].table.updateFrequency(pay.row_ind, pay.value)
      break

    case "addRow":
      state.company[pay].table.addRow()
      break

    case "createPart2Data":
      state.company[1].table.createPart2Data(state.company[0].table.rows);
      break

    case "addItemToBBufer":
      const ind = state.bbufer.indexOf(pay)
      if (ind < 0)
        state.bbufer.push(pay)
      break

    case "setCellOnFocus":
      state.cellOnFocus = pay
      break

    case "changeBbuferItem":
      if (state.cellOnFocus) {
        const {
          p,
          x,
          y
        } = state.cellOnFocus
        state.company[p].table.setCell(x, y, pay)
      }
      break

    case "setStateFromStorage":
      const storage = JSON.parse(sessionStorage["AD-builder-state"])
      state.company.forEach((t, i) => {
        state.company[i].table.rows = storage.company[i].table.rows;
      })
      break

    default:
      throw new Error();
  }
  return {
    ...state
  }
}