import { useContext } from "react";
import { AppContext } from "../App"
import { Col } from "./column";

export function Table(props) {
  const { state, dispatch } = useContext(AppContext);

  const addRow = () => {
    dispatch({
      type: "addRow",
      payload: props.part
    })
  }

  return (
    <div className="table-wrapper">
      <div className="table">
        <Col key="00" type="text" spellcheck={true} width="100" part="0" col="0" title="Ключевые фразы" />
        <Col key="01" type="text" spellcheck={true} width="100" part="0" col="1" title="Группы ключевых слов" />
        <Col key="02" type="text" spellcheck={true} width="100" part="0" col="2" title="Название компаний" />
        <Col key="03" type="number" spellcheck={false} width="50" part="0" col="3" title="YW" />
        <Col key="04" type="number" spellcheck={false} width="50" part="0" col="4" title='"YW"' />
      </div>
      <span onClick={addRow} className="add-row">+</span>
    </div>
  )
}