import { useState } from "react";
import { useContext } from "react";
import { AppContext } from "../App";


export function BBufer() {
  const { state, dispatch } = useContext(AppContext)
  const [open, setOpen] = useState(false)

  const change = (v) => {
    dispatch({
      type: "changeBbuferItem",
      payload: v
    })
  }

  const list = state.bbufer.map(n => {
    return <div onClick={() => change(n)} key="n" className="list-item pointer">{n}</div>
  })

  if (state.bbufer.length === 0) return null

  if (!open) {
    return (
      <div onClick={() => setOpen(true)} className="bbufer close">B</div>
    )
  }

  return (
    <div className="card bbufer">
      <div className="card-header tac">B-буфер</div>
      <div className="card-body">{list}</div>
      <div onClick={() => setOpen(false)} className="card-footer pointer">
        Закрыть
      </div>
    </div>
  )
}