import { useContext } from "react";
import { AppContext } from "../App"
import { Columns } from "./cols";
import { Col } from "./column";

export function Table_2(props) {
  const { state, dispatch } = useContext(AppContext);

  const addRow = () => {
    dispatch({
      type: "addRow",
      payload: props.part
    })
  }

  return (
    <div className="table-wrapper">
      <div className="table">
        <Columns data={[
          { type: "number", spellcheck: false, width: 30, part: 1, col: 3, title: "YW" },
          { type: "number", spellcheck: false, width: 30, part: 1, col: 4, title: '"YW"' },
          { type: "text", spellcheck: true, width: 80, part: 1, col: 0, title: "Ключи" },
          { type: "text", spellcheck: true, width: 80, part: 1, col: 1, title: "Группы" },
          { type: "text", spellcheck: true, width: 120, part: 1, col: 2, title: "Заголовки" },
          { type: "text", spellcheck: false, width: 80, part: 1, col: 5, title: "Ссылка" },
        ]} />
      </div>
      {/* <span onClick={addRow} className="add-row">+</span> */}
    </div>
  )
}