

export function UpDownBtns() {

  const up = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth"
    });
  }

  const down = () => {
    window.scrollTo({
      top: document.body.offsetHeight,
      behavior: "smooth"
    });
  }

  return (
    <div className="updown-bar">
      <div onClick={up} className="btn up-btn">up</div>
      <div onClick={down} className="btn down-btn">down</div>
    </div>
  )
}