import { useReducer, createContext, useState, useEffect } from 'react';
import {
  HashRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import './App.scss';
// Components
import { reducer } from './c/reducer';
import { Topbar } from './c/topbar';
import { Table } from './c/table';
import { Table_2 } from './c/table_2';
import TableClass from './c/tableClass';
import { ControlBar_1 } from './c/controlbar_1';
import { ControlBar_2 } from './c/controlbar_2';
import { FAQ } from './c/faq';
import { UpDownBtns } from './c/up_down_btns';
import { BBufer } from './c/bbufer';
import { SyncSession } from './c/syncSession';

export let AppContext = createContext(null);

function App() {

  const [state, dispatch] = useReducer(reducer, {
    // OPTIONS
    walletCount: 3,
    method: "STAG",
    quotable: "Нет",
    bbufer: [],
    // PARTs
    company: [
      {
        table: new TableClass(5, 0).init()
      },
      {
        table: new TableClass(7, 1).init()
      }
    ],
  })

  const [init, setInit] = useState(false)

  useEffect(() => {
    document.body.addEventListener('mouseup', (e) => {
      dispatch({
        type: "copyModOff"
      })
    })
    document.body.addEventListener("click", () => {
      const openMenus = document.querySelectorAll(".options.open")
      openMenus.forEach((om) => {
        om.classList.remove('open')
      })
    })
    setInit(true)

    if (sessionStorage.getItem("AD-builder-state") !== null) {
      dispatch({ type: "setStateFromStorage" })
    }

    window.o = { method: "STAG" }
  }, [init])

  return (

    <AppContext.Provider value={{ state, dispatch }}>
      <Topbar />
      <Router>
        <Routes>

          <Route path="/" element={
            <div className="part">
              <Table part="0" />
              <ControlBar_1 part="0" />
            </div>
          } />

          <Route path="/part_2" element={
            <div className="part">
              <Table_2 part="1" />
              <ControlBar_2 part="0" />
            </div>
          } />

        </Routes>
      </Router>
      <UpDownBtns />
      <SyncSession />
      <FAQ />
      <BBufer />
    </AppContext.Provider>

  );
}

export default App;