class TableClass {
  constructor(n, p) {
    this.colsCount = n
    this.partIndex = p
    this.selectedCell = null
    this.rows = []
    this.colIndexesToReplace = []
  }

  setCell(x, y, data) {
    while (this.rows.length <= y) {
      this.rows.push(this.newRow());
    }
    if (this.rows[y] !== undefined && this.rows[y].cells[x] !== undefined) {
      this.rows[+y].cells[+x].data = data;
    }
  }

  getCell(x, y) {
    return this.rows[y].cells[x]
  }

  setCol(x, data) {
    for (let i = 0; i < data.length; i++) {
      this.setCell(x, i, data[i]);
    };
    return this.rows;
  }

  getRow(y) {
    return this.rows[y];
  }

  getCol(col_index) {
    let col = []
    this.rows.forEach((row) => {
      col.push(row.cells[col_index])
    })
    return col
  }

  toggleRowDisable(y, value) {
    this.rows[y].cells.forEach((cell) => {
      cell.options.disabled = !value;
    })
    return this.rows;
  }

  newCell() {
    return {
      data: '',
      options: {
        disabled: false, // Отключено
      }
    }
  }

  newRow() {
    let r = {
      frequency: "", // Частотность
      cells: []
    };
    for (let n = 0; n < this.colsCount; n++) {
      r.cells.push(this.newCell());
    }
    return r;
  }

  addRow() {
    this.rows.push(this.newRow())
  }

  setSelectedCell(val) {
    this.selectedCell = val
  }

  clearCol(col) {
    this.rows.forEach((row) => {
      row.cells[col].data = ""
    })
  }

  clearAll() {
    this.rows.forEach(r => {
      r.cells.forEach(c => {
        c.data = ""
      })
    })
  }

  sortColumn(col_index, sort_title) {
    switch (sort_title) {

      case "upper_numeric":
        this.rows.sort((a, b) => {
          return a.cells[col_index].data - b.cells[col_index].data
        })
        return

      case "lower_numeric":
        this.rows.sort((a, b) => {
          return b.cells[col_index].data - a.cells[col_index].data
        })
        return

      case "upper_alphabetical":
        this.rows.sort((a, b) => {
          const strA = a.cells[col_index].data.toLowerCase(),
            strB = b.cells[col_index].data.toLowerCase()
          if (strA < strB) return -1
          if (strA > strB) return 1
          return 0
        })
        return

      case "lower_alphabetical":
        this.rows.sort((a, b) => {
          const strA = a.cells[col_index].data.toLowerCase(),
            strB = b.cells[col_index].data.toLowerCase()
          if (strB < strA) return -1
          if (strB > strA) return 1
          return 0
        })
        return

      default:
        throw new Error();
    }
  }

  cut_square_brackets() {
    this.rows.forEach(row => {
      row.cells.forEach(cell => {
        cell.data = cell.data.replace(/\[.*?\]/g, '');
        cell.data = this.deleteOverSpace(cell.data)
      })
    })
  }

  deleteOverSpace(str) {
    return str.replace(/\s+/g, ' ').trim()
  }

  replace(from, to) {
    this.rows.forEach(row => {
      row.cells.forEach((cell, ind) => {
        if (this.colIndexesToReplace[ind]) {
          cell.data = cell.data.replace(from, to);
        }
      })
    })
  }

  changeColFromReplaceBar(colIndex, value) {
    this.colIndexesToReplace[colIndex] = !value
  }

  updateFrequency(rowInd, val) {
    this.rows[rowInd].frequency = val
  }

  quotable(frequencyTitle) {
    const fr = frequencyTitle.split('/');
    this.rows.forEach(row => {
      if (fr[0] === row.frequency || fr[1] === row.frequency) { // ковычим согласно частотности
        row.cells.forEach((cell, ind) => {
          if (ind === 0 && cell.data.length > 0 && cell.data[0] !== '"') {
            cell.data = '"' + cell.data + '"';
          }
        })
      }
    })
  }

  unquotable() {
    this.rows.forEach(row => {
      row.cells.forEach((cell, ind) => {
        cell.data = cell.data.replace(/"/g, "")
      })
    })
  }

  createPart2Data(rows) {
    const _rows = [];
    rows.forEach(r => {
      let _cells = []

      r.cells.forEach((c, i) => {
        _cells.push(this.newCell())
        _cells[i].data = c.data
        _cells[i].options = c.options
      })

      _rows.push({
        cells: _cells,
        frequency: r.frequency
      })
    })
    // Добавляем частотку в названия групп
    // Если STAG, то частотку
    _rows.forEach((r) => {
      if (r.frequency !== "-" && r.cells[0].data !== "") {
        r.cells[1].data = r.frequency + " / " + r.cells[1].data
      }
    })

    this.rows = _rows;
  }

  init() {
    // add rows to table
    for (let i = 0; i < 10; i++) {
      this.rows.push(this.newRow());
    }

    for (let n = 0; n < this.colsCount; n++) {
      this.colIndexesToReplace.push(false)
    }

    return this;
  }
}

export default TableClass;