import { Col } from "./column";


export function Columns(props) {

    const cols = props.data.map((c, i) => {
        return <Col
            key={i}
            type={c.type}
            spellcheck={c.spellcheck}
            width={c.width}
            part={c.part}
            col={c.col}
            title={c.title} />
    })

    return cols;
}