import { useContext } from "react";
import { AppContext } from "../App";
import { Cell } from "./cell";
import 'bootstrap-icons/font/bootstrap-icons.css';
import { useState } from "react";
import { useRef } from "react";

export function Col(props) {
  const { state, dispatch } = useContext(AppContext)
  const [menuStatus, setMenuStatus] = useState(false)
  const colOptions = useRef(null);
  let col = 'нет данных';

  if (state.company[props.part] === undefined) {
    return null;
  }

  if (state.company[+props.part].table !== undefined) {
    col = state.company[+props.part].table.rows.map((c, ind) => {
      return <Cell
        spellcheck={props.spellcheck}
        type={props.type}
        key={props.part + props.col + ind}
        index={ind}
        {...props}
        data={c.cells[props.col]}
        position={{ p: props.part, x: props.col, y: ind }} />
    })
  }

  const onClick = (e) => {
    e.stopPropagation()
    document.querySelectorAll(".options.open").forEach((m) => {
      m.classList.remove("open")
    })
    if (colOptions.current.classList.contains('open')) {
      colOptions.current.classList.remove('open')
    }
    else {
      colOptions.current.classList.add('open')
    }
  }

  return (
    <div width={props.width} className="col-wrapper">
      <div className="th">
        <span>{props.title}</span>
        <div className="options" ref={colOptions} onClick={onClick}>
          <i className="bi-three-dots-vertical"></i>
          <Menu {...props} />
        </div>
      </div>
      {col}
    </div>
  )
}

function Menu(props) {
  const { state, dispatch } = useContext(AppContext)

  const insertFromCP = async () => {
    let text = await navigator.clipboard.readText();
    dispatch({
      type: 'copyFromClipboard',
      payload: {
        part: props.part,
        col: props.col,
        data: text
      }
    });
  }

  const change = (e, cb) => {
    e.stopPropagation()
    e.target.closest('.options.open').classList.remove('open')
    cb();
  }

  const changeSort = (sort) => {
    dispatch({
      type: 'sortColumn',
      payload: {
        part_index: props.part,
        col_index: props.col,
        sort_title: sort
      }
    })
  }

  return (
    <div className="menu">

      <div className="menu-item" onClick={(e) => change(e, insertFromCP)}>
        <i className="bi bi-clipboard-plus"></i>
        <span>Вставить из буфера</span>
      </div>

      <div className="menu-item has-children">
        <i className="bi bi-funnel"></i>
        <span>Сортировать</span>

        <div className="menu sub-menu">
          <div onClick={() => changeSort('upper_numeric')} className="menu-item"><span>{'>'}</span></div>
          <div onClick={() => changeSort('lower_numeric')} className="menu-item"><span>{'<'}</span></div>
          <div onClick={() => changeSort('upper_alphabetical')} className="menu-item"><span>А-Я</span></div>
          <div onClick={() => changeSort('lower_alphabetical')} className="menu-item"><span>Я-А</span></div>
        </div>

      </div>

      <div className="delimeter"></div>

      <div className="menu-item" onClick={(e) => change(e, () => dispatch({
        type: "clearCol",
        payload: {
          part: props.part,
          col: props.col
        },
      }))}>
        <i className="bi bi-x-octagon"></i>
        <span>Очистить колонку</span>
      </div>

    </div>
  )
}