import { useState, useEffect } from "react";
import { useContext } from "react";
import { AppContext } from "../App";


export function SyncSession() {
    const { state, dispatch } = useContext(AppContext)
    const [synch, setSynchStatus] = useState(false)
    const icon = synch ? <i className="bi bi-arrow-repeat spin"></i> : <i className="bi bi-cloud"></i>

    useEffect(() => {
        if (!synch) {
            setTimeout(() => {
                sessionStorage.setItem("AD-builder-state", JSON.stringify(state))
                setSynchStatus(true)
                setTimeout(() => {
                    setSynchStatus(false)
                }, 2000)
            }, 1 * 60000)
        }
    }, [synch])

    return (
        <div className="synch">
            {icon}
        </div>
    )
}